.dpd-parcelshop-popup {
	display: none;
	position: fixed;
	z-index: 999;
	width: 100%;
	height: 100%;
	background: rgb(0 0 0 / 50%);
	top: 0;
	left: 0;
	z-index: 100000;

	// Force stronger selector to override external styles
	&.active {
		display: block;
	}

	& #{&} {
		&__close-btn {
			position: absolute;
			font-size: 0;
			right: 20px;
			top: 20px;
			color: #fff;
			background: #dc0032;
			border: 0;
			-webkit-box-shadow: none;
			box-shadow: none;
			outline: none;
			padding: 0;
			height: 50px;
			width: 50px;
			display: block;
			z-index: 2;

			&:before,
			&:after {
				content: '';
				display: block;
				position: absolute;
				background: #fff;
				width: 2px;
				height: 26px;
				top: 0;
				bottom: 0;
				margin: auto;
				left: 0;
				right: 0;
			}

			&::before {
				transform: rotate(-45deg);
			}

			&::after {
				transform: rotate(45deg);
			}
		}

		&__container {
			-ms-flex-item-align: center;
			-ms-grid-row-align: center;
			align-self: center;
			position: relative;
			height: 100%;
			width: 100%;
			padding: 15px;
		}

		&__content {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-orient: vertical;
			-webkit-box-direction: normal;
			-ms-flex-direction: column;
			flex-direction: column;
			position: relative;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			-ms-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			max-width: 500px;
			width: 100%;
			height: auto;
			overflow: hidden;
			border-radius: 0px;
			background: #ffffff;
			padding: 15px;
		}

		&__form {
			margin-bottom: 0;
		}

		&__row {
			margin-bottom: 15px;
		}

		&__input-wrap {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-ms-flex-align: center;
			align-items: center;
			margin-bottom: 5px;
		}

		&__label {
			min-width: 100px;
			margin-bottom: 0;
		}

		&__input,
		&__input:hover,
		&__input:focus {
			width: 100%;
			padding: 5px;
			background: #ffffff;
			outline: none;
			border: 1px solid #ccc;
			background: #fff;
			line-height: normal;
			color: #000;
			box-shadow: none;
		}

		&__input {
			cursor: pointer;
		}

		&__search-btn,
		&__choose-btn {
			color: #fff;
			background: #dc0032;
			border: 0;
			-webkit-box-shadow: none;
			box-shadow: none;
			outline: none;
			padding: 0 14px;
			width: 100%;
			cursor: pointer;
			height: 40px;
			line-height: normal;
			box-sizing: border-box;
		}

		&__search-btn.loading {
			position: relative;
		}

		&__search-btn.loading:before {
			content: '';
			z-index: 1;
			background: rgba(255, 255, 255, 0.8);
			width: 100%;
			height: 100%;
			position: absolute;
			left: 0;
			top: 0;
		}

		&__search-btn.loading:after {
			-webkit-animation: dpd-parcelshop-input-loading-animation 1.2s linear
				infinite;
			animation: dpd-parcelshop-input-loading-animation 1.2s linear infinite;
			border-color: #dc0032 transparent;
			border-radius: 50%;
			border-style: solid;
			border-width: 1px;
			content: ' ';
			display: block;
			height: 16px;
			position: absolute;
			width: 16px;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			margin: auto;
			z-index: 2;
		}

		&__results {
			display: none;
		}

		&__results.active {
			display: block;
		}

		&__parcels-list {
			max-height: 120px;
			overflow: auto;
			margin-bottom: 15px;
			margin-top: 15px;
			margin-left: 0;
			padding: 0;
		}

		&__parcels-list li {
			cursor: pointer;
			color: #000;
			padding: 5px;
			font-size: 14px;
			border: 1px solid #ccc;
			border-bottom-width: 0;
			-webkit-transition: background-color 0.2s linear, color 0.2s linear;
			-o-transition: background-color 0.2s linear, color 0.2s linear;
			transition: background-color 0.2s linear, color 0.2s linear;
		}

		&__parcels-list li:last-child {
			border-bottom-width: 1px;
		}

		&__parcels-list li:hover {
			background-color: #ccc;
		}
		&__parcels-list li.active {
			background-color: #dc0032;
			color: #fff;
		}

		&__response {
			display: none;
			text-align: center;
		}

		&__response.active {
			display: block;
		}

		&__response p {
			margin-top: 15px;
			font-size: 16px;
			color: red;
			margin-bottom: 15px;
		}
	}
}

@keyframes dpd-parcelshop-input-loading-animation {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}
